import Vue from "vue";
import Router from "vue-router";

const routes = [
  {
    name: "zi",
    path: "/zi",
    component: () => import("@/zi"),
  },
  {
    name: "zlogs",
    path: "/zlogs",
    component: () => import("@/zlogs"),
  },
  {
    name: "enter",
    path: "/enter",
    component: () => import("@/enter"),
  },
  {
    path: "/",
    component: () => import("@/enter"),
  },
  {
    name: "404",
    path: "/404",
    component: () => import("@/nothing"),
  },
  {
    name: "403",
    path: "/403",
    component: () => import("@/nothing"),
  },
  {
    path: "*",
    component: () => import("@/nothing"),
  },
];

Vue.use(Router);

export default new Router({
  // mode: 'history',
  routes
});
